@import "iboot/iboot";
html,
body {
    background-color: white;
}

.main {
    padding-top: 48px;
}

.container {
    max-width: 960px;
}

.navbar {
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #3b3e43;
    background-color: #000000;
    background-color: #1c1c20;
    a {
        color: rgba(255, 255, 255, .8);
        font-size: 12px;
        line-height: 48px;
        &:hover {
            color: rgba(255, 255, 255, 1);
        }
    }
    .logo {
        float: left;
        width: 62px;
        a {
            font-size: 20px;
            font-weight: bold;
        }
        img {
            @include img-fluid;
            height: 48px;
            width: auto;
        }
    }
    .user-nav {
        @media(min-width: map-get($breakpoints, lg)) {
            float: right;
        }
    }
}

.nav {
    @include flex;
}

.nav-item {
    @include flex-item;
    max-width: 120px;
}

.col-left {
    @media (min-width: map-get($breakpoints, lg)) {
        padding-right: 30px;
        max-width: 70%;
    }
}

.col-right {
    @media (min-width: map-get($breakpoints, lg)) {
        max-width: 30%;
    }
}
