$blue:    #007bff !default;
$indigo:  #6610f2 !default;
$purple:  #6f42c1 !default;
$pink:    #e83e8c !default;
$red:     #dc3545 !default;
$orange:  #fd7e14 !default;
$yellow:  #ffc107 !default;
$green:   #28a745 !default;
$teal:    #20c997 !default;
$cyan:    #17a2b8 !default;
// Colors
$gray-dark: #373a3c !default;
$gray: #55595c !default;
$gray-light: #b4b4b4 !default;
$gray-lighter: #c8c8c8 !default;
$gray-lightest: #f0f0f0 !default;
//
$brand-primary: #4285f4 !default;
$brand-success: #34a853 !default;
$brand-info: #20A0FF !default;
$brand-warning: #fbbc05 !default;
$brand-danger: #ea4335 !default;
$brand-inverse: $gray-dark !default;
//=========================================================
$space: 15px !default;
//=========================================================
// breakpoints
$breakpoints: ( xs: 0, sm: 576px, md: 768px, lg: 960px, xl: 1200px) !default;
// containers
$container-max-widths: ( sm: 540px, md: 720px, lg: 960px, xl: 1140px) !default;
//=========================================================
$enable-flex: false !default;
$enable-rounded: true !default;
$enable-shadows: true !default;
$enable-gradients: true !default;
$enable-transitions: true !default;
$enable-hover-media-query: true !default;
$enable-grid-classes: true !default;
$enable-print-styles: true !default;
//=========================================================
$form-icon-success-color: $brand-success !default;
