html {
    // 1. Change the default font family in all browsers (opinionated).
    // 2. Correct the line height in all browsers.
    // 3. Prevent adjustments of font size after orientation changes in IE and iOS.
    box-sizing: border-box;
    font-family: sans-serif; // 1
    -ms-text-size-adjust: 100%; // 3
    -webkit-text-size-adjust: 100%; // 3
    -ms-overflow-style: scrollbar;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

*,
*::after,
*::before {
    box-sizing: inherit;
}

body {
    margin: 0; // Remove the margin in all browsers (opinionated).
    font-family: sans-serif, monospace, Helvetica, Arial, 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', 'WenQuanYi Micro Hei';
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}


// link

a {
    // 1. Remove the gray background on active links in IE 10.
    // 2. Remove gaps in links underline in iOS 8+ and Safari 8+.
    color: $brand-primary;
    text-decoration: none;
    background-color: transparent; // 1
    -webkit-text-decoration-skip: objects; // 2
    &:hover {
        color: darken($brand-primary, 10);
        text-decoration: inherit;
    }
    &:focus {
        outline: none;
        outline-offset: none;
    }
    /*
    Remove the outline on focused links when they are also active or hovered
    in all browsers (opinionated).
    */
    &:active,
    &:hover {
        outline-width: 0;
    }
}

a:not([href]):not([tabindex]) {
    color: inherit;
    text-decoration: none;
    @include hover-focus {
        color: inherit;
        text-decoration: none;
    }
    &:focus {
        outline: none;
    }
}

// media

img {
    vertical-align: middle;
    line-height: 1;
    /* Remove the border on images inside links in IE 10-. */
    border-style: none;
}

svg:not(:root) {
    //Hide the overflow in IE.
    overflow: hidden;
}


// form

button,
input,
optgroup,
select,
textarea {
    // 1. Change font properties to `inherit` in all browsers (opinionated).
    // 2. Remove the margin in Firefox and Safari.
    font: inherit; // 1
    margin: 0; // 2
}

button,
input {
    // Show the overflow in IE.
    // 1. Show the overflow in Edge.
    overflow: visible; // 1
}

input:-webkit-autofill,
select:-webkit-autofill,
textarea:-webkit-autofill {
    background-color: inherit;
}

input[type="date"],
input[type="datetime-local"],
input[type="month"],
input[type="time"] {
    -webkit-appearance: listbox;
}

input[type="search"] {
    -webkit-appearance: none;
}

input[type="checkbox"],
input[type="radio"] {
    &:disabled {
        cursor: not-allowed;
    }
}

[hidden] {
    display: none !important;
}

[tabindex="-1"]:focus {
    outline: none !important;
}

[role="button"] {
    cursor: pointer;
}

[role="button"],
a,
area,
button,
input,
label,
select,
summary,
textarea {
    touch-action: manipulation;
}

label {
    display: inline-block;
}

button:focus {
    outline: 1px dotted;
    outline: 5px auto -webkit-focus-ring-color;
}

button,
input,
select,
textarea {
    line-height: inherit;
}

textarea {
    resize: vertical;
}

fieldset {
    min-width: 0;
    padding: 0;
    margin: 0;
    border: 0;
}

legend {
    display: block;
    width: 100%;
    padding: 0;
    margin-bottom: 0.5rem;
    font-size: 1.5rem;
    line-height: inherit;
}


//

optgroup {
    // Restore the font weight unset by the previous rule.
    font-weight: bold;
}


// table

table {
    border-collapse: collapse;
    background-color: transparent;
}

caption {
    padding-top: 15px;
    padding-bottom: 15px;
    color: red;
    text-align: left;
    caption-side: bottom;
}

th {
    text-align: left;
}


abbr[data-original-title],
abbr[title] {
    cursor: help;
    border-bottom: 1px dotted $brand-primary;
}

address {
    margin-bottom: 1rem;
    font-style: normal;
    line-height: inherit;
}

output {
    display: inline-block;
}


figure {
    margin: 0 0 1rem;
}


//

hr {
    // 1. Add the correct box sizing in Firefox.
    // 2. Show the overflow in Edge and IE.
    margin: 4px auto;
    border: 0;
    border-top: 1px solid gray;
    box-sizing: content-box; // 1
    height: 0; // 1
    overflow: visible; // 2
}

mark {
    padding: 15px;
    background-color: #ff0;
    color: #000;
}

small {
    font-size: 80%;
    font-weight: normal;
}

blockquote {
    margin: 0;
    padding: 15px;
    border-left: 4px solid $brand-primary;
}

b,
strong {
    // Add the correct font weight in Chrome, Edge, and Safari.
    font-weight: bolder;
}


// Code
// Inline and block code styles
code,
kbd,
pre,
samp {
    $font-family-monospace: monospace, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New" !default;
    font-family: $font-family-monospace;
}

code {
    padding: 0.4rem 0.2rem;
    font-size: 90%;
    color: #bd4147;
    background-color: #f7f7f9;
    border-radius: 4px;
}

// User input typically entered via keyboard
kbd {
    padding: 0.4rem 0.2rem;
    font-size: 90%;
    color: white;
    background-color: #333;
    border-radius: 2px;
    box-shadow: 0 0 6px 5px rgba(0, 0, 0, .3);
    kbd {
        padding: 0;
        font-size: 100%;
        font-weight: bold;
        box-shadow: 0 0 6px 5px rgba(0, 0, 0, .3);
    }
}

// Blocks of code
pre {
    display: block;
    margin-top: 0;
    margin-bottom: 1rem;
    font-size: 80%;
    color: gray;
    overflow: auto;
    code {
        padding: 0;
        font-size: inherit;
        color: inherit;
        background-color: transparent;
        border-radius: 0;
    }
}
